.footer-distributed {
    background: #000;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
    box-sizing: border-box;
    width: 100%;
    text-align: left;
    font: bold 18px sans-serif;
    padding: 55px 50px;
    margin-top: 5%;
    
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
    display: inline-block;
    vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left {
    width: 40%;
}

/* The company logo */

.footer-distributed h3 a {
    color: #ffffff;
    font-size: 3.8rem;
    margin: 0;
}

.footer-distributed h3 span {
    cursor: pointer;
    color: rgb(1, 114, 114);
    font-size: 2.5rem;
    font-weight: 400;
    text-transform: uppercase;
    background: -webkit-linear-gradient(#eee,teal);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Footer links */

.footer-distributed .footer-links {
    color: #ffffff;
    margin: 20px 0 12px;
    padding: 0;
    font-size: 1.9rem;
} 

.footer-distributed .footer-links a {
    display: inline-block;
    line-height: 1.8;
    font-weight: 400;
    text-decoration: none;
    color: inherit;
}

.footer-distributed .footer-company-name {
    color: #878787;
    font-size: 14px;
    font-weight: normal;
    margin: 0;
}

.footer-distributed .footer-company-name a:hover {
   font-size: 15px;
   font-weight: bold;
   color: teal !important;
}

/* Footer Center */
.Portfolio .PortfolioIcon {
    color: #ffffff;
    font-size: 25px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    margin: 10px 15px 20px;
    vertical-align: middle;
}
.Portfolio a{
    color: teal;
    text-decoration: none;
    font-size: 1.9rem;
}


.footer-distributed .footer-center {
    width: 35%;
}

.footer-distributed .footer-center .icon {
    color: #ffffff;
    font-size: 25px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    margin: 10px 15px;
    vertical-align: middle;
}

.footer-distributed .footer-center p {
    display: inline-block;
    color: #ffffff;
    font-weight: 400;
    vertical-align: middle;
    margin: 0;
}

.footer-distributed .footer-center p span {
    display: block;
    font-weight: normal;
    font-size: 1.4rem;
    line-height: 2;
}

.footer-distributed .footer-center p a {
    color: teal;
    text-decoration: none;
    font-size: 1.9rem;
}

.footer-distributed .footer-links a:before {
    content: "|";
    font-weight: 300;
    font-size: 20px;
    left: 0;
    color: #fff;
    display: inline-block;
    padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
    content: none;
}

/* Footer Right */

.footer-distributed .footer-right {
    width: 20%;
}

.footer-distributed .footer-company-about {
    line-height: 20px;
    color: #92999f;
    font-size: 1.4rem;
    font-weight: normal;
    margin: 0;
}

.footer-distributed .footer-company-about span {
    display: block;
    color: #ffffff;
    font-size: 2.2rem;
    font-weight: bold;
    margin-bottom: 20px;
}

.footer-distributed .footer-icons {
    margin-top: 25px;
}

.footer-distributed .footer-icons .footerIcon {
    display: inline-block;
    width: 35px;
    height: 35px;
    cursor: pointer;
    background-color: rgb(20, 114, 114);
    border-radius: 5px;
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;
    margin-right: 20px;
    margin-bottom: 5px;
}

.footer-distributed .footer-icons .footerIcon:hover {
    transition: 0.9s;
    border-radius: 8px;
    background-color: #fff;
    color: rgb(20, 114, 114);
}


@media (max-width: 880px) {

    .footer-distributed {
        font: bold 1.6rem sans-serif;
    }

    .footer-distributed .footer-center p span {
        font-size: 1.5rem;
    }

    .footer-distributed .footer-left,
    .footer-distributed .footer-center,
    .footer-distributed .footer-right {
        display: block;
        width: 100%;
        margin-bottom: 40px;
        text-align: center;
    }

    .footer-distributed .footer-center i {
        margin-left: 0;
    }

}