 * {
   margin: 0;
   padding: 0;
   font-family: sans-serif;
 }

 html {
   font-size: 62.5%;
 }

 a {
   text-decoration: none;
 }

 li {
   text-decoration: none;
   list-style: none;
 }

 .logo h2 {
   font-size: 2.5rem;
   font-weight: 600;
   text-transform: uppercase;
   background: -webkit-linear-gradient(#eee, teal);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
 }

 .logo h2 span {
   font-size: 3.5rem;
 }

 .menu-link ul li {
   font-size: 1.9rem;
 }

 .menu-link ul li a {
   text-transform: capitalize;
   color: rgb(0, 0, 3, 0.8);
   transition: 0.5s;
 }

 .menu-link ul li:hover>a {
   transform-origin: left;
   color: rgba(0, 0, 3, 1);
   text-decoration: underline;
   transition: 0.5s;
 }


 .profile ul li a {
   text-transform: capitalize;
   color: rgb(0, 0, 3, 0.8);
   transition: 0.5s;
 }

 .profile ul li:hover>a {
   transform-origin: left;
   color: rgba(0, 0, 3, 1);
   text-decoration: underline;
   transition: 0.5s;
 }

 .profile ul li {
   font-size: 1.9rem;
 }

 .main-nav {
   width: 100%;
   height: 8rem;
   display: grid;
   /* grid-template-columns: 10rem 1fr 2fr 2fr 10rem; */
   grid-template-columns: 10rem 1fr 3fr 1fr 10rem;
   box-shadow: rgb(50, 50, 93, 0.25) 0px 50px 100px -20px,
     rgba(0, 0, 0, 0.3) 0px 10px 60px -30px;
 }

 .logo {
   display: grid;
   grid-column: 2/3;
   justify-content: start;
   align-items: center;
 }

 .menu-link {
   grid-column: 3/4;
 }

 .menu-link ul {
   height: 8rem;
   display: flex;
   justify-content: space-between;
   align-items: center;
 }

 .topImg {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   object-fit: cover;
   margin-right: 15px;
   cursor: pointer;
 }

 .profile {
   grid-column: 4/5;
 }

 .profile ul {
   font-size: 1.8rem;
   height: 8rem;
   display: grid;
   grid-template-columns: 3fr repeat(2, 5fr);
   align-items: center;
   justify-content: flex-end;
 }

 .profile ul li {
   text-align: right;
 }

 .profile ul li:first-child {
   grid-column: 2/3;
 }

 .hamburger-menu {
   display: none;
 }

 .menu-desktop {
   display: none;
 }

 .DesktopLoginStyle {
   display: none;
 }


 @media (max-width:1080px) {
   .main-nav {
     height: 8rem;
     grid-template-columns: 2rem 1fr 3fr 2fr 2rem;
   }

   .logo,
   .menu-link ul,
   .profile ul {
     height: 8rem;
   }

 }

 @media(max-width:850px) {
   .DesktopLoginStyle {
     display: flex;
   }

   .hamburger-menu {
     display: flex;
   }

   .menu-desktop {
     display: flex;
   }

   .main-nav {
     height: 7rem;
     grid-template-columns: 2rem 1fr 3fr 2fr 2rem;
   }

   .menu-link {
     display: none;
   }

   .profile-mobile {
     display: none;
   }

   .logo,
   .menu-link ul,
   .profile ul {
     height: 7rem;
   }

   .mobile-menu-link {
     grid-column: 2/4;
     position: relative;
     z-index: 99;
   }
   
   .mobile-menu-link {
    /* ------------------------ */
     margin-top: -1px;
     grid-column: 4/1;
     width: 85px;
     /* ---------------------- */
     /* grid-column: 2/5; */
     background-color: white;
     height: 33rem;
     display: grid;
     align-items: center;
     padding-left: 3rem;
     transition: all 2s linear;
     transform-origin: top;
     box-shadow: rgb(50, 50, 93, 0.25) 0px 50px 100px -20px,
       rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
   }

   .mobile-menu-link ul {
     align-items: start;
     flex-direction: column;
     height: 33rem;
     display: flex;
     justify-content: space-around;
   }

   .mobile-menu-link ul li:first-child {
     transition-delay: 0.2s;
   }

   .profile-mobile {
     grid-column: 2/4;
     position: relative;
     z-index: 99;
   }

   .profile {
     grid-row: 1/2;
     grid-column: 3/5;
     justify-content: end;
     align-items: center;
     transition: all 2s linear;
   }

   .profile .profile-desktop {
     height: 0;
     display: none;
   }

   .profile {
     display: flex;
     height: 7rem;
     justify-self: end;
     align-items: center;
   }

   .profile .profile-desktop {
     display: block;
     font-size: 2.5rem;
   }

   .profile ul {
     font-size: 1.8rem;
     height: 6rem;
     display: grid;
     grid-template-columns: 2fr repeat(3, );
     align-items: center;
     justify-content: flex-end;
   }
 }