.reviewsHeading {
    color: #000000be;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.226);
    width: 20vmax;
    margin-top: 4vmax;
}

.reviews {
    display: flex;
    overflow: auto;
    margin-top: 35px;
    transition: ease-in-out
}

.reviewCard {
    flex: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
    border: 1px solid rgba(56, 56, 56, 0.116);
    width: 23vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 1vmax;
}

.reviewCard>img {
    width: 25%;
    margin-top: 12px;
    border-radius: 50%;
}

.reviewCard>p {
    margin-top: 12px;
    color: rgba(0, 0, 0, 0.836);
    font-family: "Varela Round", Arial, Helvetica, sans-serif !important;
    font-size: 14px !important;
}

.reviewCardComment {
    color: rgba(0, 0, 0, 0.445);
    font: 400 16px "Lora", serif !important;
    font-style: italic !important;
    line-height: 24px !important;
    overflow: hidden;
    overscroll-behavior-inline: none;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}



.reviewCardDate {
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 13px;
    font-weight: 400;
    color: #999999;
    margin-top: 5px;
}

.reviewCardButton {
    border: none;
    background-color: teal;
    font: 500 14px "Varela Round", Arial, Helvetica, sans-serif !important;
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax 0;
    color: white;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    height: fit-content;
}

.reviewCardButton:hover {
    background-color: rgb(22, 97, 97);
    transform: scale(1.1);
}



@media screen and (max-width: 1292px) {
    .reviewCard {
        width: 30%;
    }
}

@media screen and (max-width: 1044px) {
    .reviewCard {
        width: 45%;
    }
   
}

@media screen and (max-width: 732px) {
    .reviewCard {
        width: 90%;
    };

   
}