.profileContainer {
  display: flex;
  height: 90vh;
  width: 100vw;
  margin-top: -5%;
  margin-bottom: 0%;
  max-width: 100%;
} 

.profileContainer>div {
  display: flex;
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profileContainer>div:first-child>h1 {
  color: ' rgb(1, 114, 114)';
  font: 500 24px "sans-serif";
  transform: translateX(-10vmax) translateY(-2vmax);
}

.updateProfileImageAvatar {
  width: 19vmax !important;
  transition: all 0.5s !important;
  height: 25vmax !important;
  background-color: white !important;
  cursor: pointer;
}

.updateProfileImage {
  width: 20vmax;
  border-radius: 100%;
  transition: all 0.5s;
  object-fit: cover;
  /* cursor: pointer; */
}

.profileInput {
  font: 400 1.8rem "sans-serif" !important;
}

.profileContainer>div:first-child>img {
  width: 20vmax;
  border-radius: 100%;
  transition: all 0.5s;
  object-fit: cover;
  /* cursor: pointer; */
}

.profileContainer>div:first-child>img:hover {
  transform: scale(1.05);
}

.profileContainer>div:last-child {
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 5vmax;
  box-sizing: border-box;
}

.profileContainer>div:last-child>div>h4 {
  color: black;
  font: 400 1.8rem "sans-serif";
}

.profileContainer>div:last-child>div>p {
  color: rgba(0, 0, 0, 0.418);
  font: 800 1.4rem cursive;
  margin: 0.2vmax;
}

/* button */
.profileButton {
  align-self: center;
  background-color: rgb(1, 114, 114) !important;
  font-size: 1.3rem !important;
  font-weight: bold !important;
  padding: 5px !important;
}

.profileButton:hover {
  background-color: teal !important;
}

.profileDelete {
  padding: 5px !important;
  background-color: rgb(129, 11, 21) !important;
  margin-top: 20px !important;
  font-size: 1.3rem !important;
  font-weight: bold !important;
}

.profileDelete:hover {
  background-color: rgb(63, 2, 8) !important;
}

.profileEdit {
  padding: 5px !important;
  width: 150px !important;
  background-color: rgb(1, 114, 114) !important;
  margin-top: 20px !important;
  font-size: 1.3rem !important;
  font-weight: bold !important;
}

.profileEdit:hover {
  background-color: teal !important;
  ;
}

@media screen and (max-width: 700px) {
  .profileContainer {
    margin-top: -20px;
    height: 120vh;
  }

  .profileContainer>div:first-child>img {
    margin-top: 40px;
  }
  .profileContainer label {
    margin-top: 40px;
  }
  .profileContainer {
    flex-direction: column;
  }

  .profileContainer>div:first-child>h1 {
    font: 500 3.2vmax "sans-serif";
    transform: translateY(-2vmax);
  }

  .profileContainer>div:last-child {
    text-align: center;
    align-items: center;
  }

  .profileContainer>div:last-child>div>h4 {
    font: 400 1.8rem "sans-serif";
  }

  .profileContainer>div:last-child>div>p {
    margin: 5vmax 0 !important;
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 16px;
    font-weight: 500;
    color: #999999;
  }
}